.digitise-container {
    width: 100%;
    background: #ffffff;
    color: #000000;
    margin-top: 10em;
}

.digitise {
    width: 90%;
    max-width: 1200px;
    margin-inline: auto;
    margin-bottom: 12em;
    text-align: center;
}

.digitise h3 {
    margin-bottom: 2em;
    font-size: 1.625rem;
    font-weight: 500;
}

.digitise button {
    font-size: 1rem;
    width: 195px;
    padding: .7em 0;
    /* background: #F2F2F2; */
    background: #ffffff;
    border: 1px solid #909090;
    border-radius: 5px;
    /* box-shadow: 0 0 5px 0 rgba(0,0,0,0.75); */
}

.digitise button:not(.join-btn):hover {
    color: #ffffff;
    background: #000000;
}

.digitise .join-btn {
    background: #000000;
    border: 1px solid #000000;
    color: #ffffff;
}

.cards-container {
    display: flex;
    flex-wrap: wrap;
justify-content: center;
    gap: 4em;
}

@media (min-width: 767px) {
    .cards-container {
        gap: 3em;
    }
}
