:root {
    --btn-width: 24px;
    --btn-height: calc(var(--btn-width) * .81);
    --rectangle-height: calc(var(--btn-width) * .1);
    --rectangle-radius: calc(var(--btn-width) * .04);
    --translate: calc(var(--btn-width) * .17);
    --transition: 0.3s;
}

.header {
    width: 90%;
    padding-block: 1em;
    max-width: 1200px;
    margin-inline: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-container {
    background: #000000;
    color: #ffffff;
    width: 100%;
}

.logo {
    font-size: 1.2rem;
    color: var(--clr-main);
}

.navbar a {
    border: 1px solid var(--clr-main);
    font-size: .9rem;
}

.join-link {
    color: var(--clr-main);
    padding: .5em 1em;
}

.login-link {
    background: var(--clr-main);
    color: #000000;
    padding: .5em 1.9em;
}

@media (max-width: 767px) {

    .navbar {
        background: #000000;
        position: fixed;
        inset: 0 0 0 0;
        z-index: 10;

        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: min(25vh, 10em);

        transform: translateX(100%);
        transition: transform 400ms ease-in-out;
    }

    .navbar a {
        font-size: 1.1rem;
    }

    .join-link {
        padding: .7em 2.1em;
    }
    
    .login-link {
        padding: .7em 3em;
    }
    
    .navbar ul {
        display: flex;
        flex-direction: column;
        gap: 1.2em;
    }

    .menu-btn {
        z-index: 1000;
        position: relative;
        width: var(--btn-width);
        height: var(--btn-height);
        border: 0;
        background: transparent;
        outline: none;
        cursor: pointer;
    }

    .rectangle {
        position: absolute;
        height: var(--rectangle-height);
        background: var(--clr-main);
        border-radius: var(--rectangle-radius);
        transition: transform var(--transition);
    }

    .rectangle-small {
        width: calc(var(--btn-width) / 2);
    }

    .rectangle-top {
        top: 0;
        left: 0;
        transform-origin: top left;
    }

    .rectangle-middle {
        top: 50%;
        left: 0;
        width: var(--btn-width);
        transform: translateY(-50%);
    }

    .rectangle-bottom {
        bottom: 0;
        right: 0;
        transform-origin: bottom right;
    }

    .rectangle-top.open {
        transform: translateX(var(--translate)) rotate(45deg);
    }

    .rectangle-middle.open {
        transform: translateY(-50%) rotate(-45deg);
    }

    .rectangle-bottom.open {
        transform: translateX(calc(var(--translate) * -1)) rotate(45deg);
    }

    .navbar.open {
        transform: translateX(0);
    }

}


@media (min-width: 768px) {

    

    .navbar ul {
        display: flex;
        gap: 1em;
    }

    .menu-btn {
        display: none;
    }
}