.about-container {
    position: relative;
    z-index: 100;
    width: 100%;
    background: #000000;
    color: #ffffff;
    overflow: hidden;
}

.about-bg-container {
    position: absolute;
    z-index: -10;
    width: 100%;
    min-width: 620px;
    max-width: 1100px;
    margin-inline: auto;
    inset: 0;
    background: url("../../../assets/512px-World_map_with_points.svg.png") no-repeat right;
    background-size: 120%;
    transform: rotate(-90deg);
}

.about {
    width: 90%;
    max-width: 1200px;
    margin-inline: auto;
    padding: 8em 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.about h3 {
    font-size: 1.875rem;
    margin-bottom: .7em;
}

.about p {
    max-width: 55ch;
}

.about a {
    color: var(--clr-main);
    border: 1px solid var(--clr-main);
    border-radius: 2px;
    margin-top: 1.2em;
    padding: .6em 3em;
}

.about a:hover {
    background: var(--clr-main);
    color: #000000;
}


@media (min-width: 600px) {
    .about {
        text-align: center;
        align-items: center;
    }

    .about-bg-container {
        background-position: center;
        transform: rotate(0);
    }

}

@media (min-width:1024px) {
    .about-bg-container {
        background-size: 100%;
    }
}