.boat-container {
    position: relative;
    width: 100%;
    color: #ffffff;
    padding: 5em 0;
    display: flex;
    justify-content: flex-end;
}

.boat {
    background: url("../../../assets/boat-mobile.svg") no-repeat left;
    background-size: cover;
    height: 14.6875rem;
    width: 100%;
    padding: 2em 4vw 2em 0;
    text-align: right;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}

.boat h3 {
    max-width: 14ch;
    font-size: 1.3rem;
}

.boat__search-bar {
    width: 85%;
    max-width: 600px;
}

.boat__search-bar input {
    order: -1;
    background: #000000;
    border-bottom: 1px solid #ffffff;
    color: #ffffff;
    text-align: right;
}

.boat__search-bar .search-btn {
    background: #ffffff;
}

.wave-bg__boat {
    position: absolute;
    z-index: -100;
    bottom: 3em;
    width: 100%;
}

.search-input:focus {
    outline: none;
}

input:focus::placeholder {
    color: transparent;
  }

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    box-shadow: 0 0 0 30px black inset !important;
    -webkit-box-shadow: 0 0 0 30px black inset !important;
}

input:-webkit-autofill{
    -webkit-text-fill-color: #ffffff !important;
}


@media (min-width: 767px) {
    .boat {
        width: 90%;
        height: 20rem;
        background: url("../../../assets/boat.svg") no-repeat left;
        background-size: cover;
        justify-content: center;
        gap: 2em;
    }

    .wave-bg__boat {
        bottom: 2em;
    }

    .boat h3 {
        max-width: 100%;
        order: -1;
    }
}

@media (min-width: 1400px) {
    .boat {
        padding-right: 7vw;
    }
}

@media (min-width: 1600px) {
    .boat {
        padding-right: 10vw;
    }
}