.welcome-container {
    width: 100%;
    background: #ffffff;
    color: #000000;
    position: relative;
}

.welcome {
    width: 90%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4em;
    margin-inline: auto;
    margin-bottom: 8em;
    padding-top: 17em;
}

.welcome h2 {
    width: 95%;
    max-width: 25ch;
    margin-bottom: 2em;
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, 0);
    font-size: 1.365rem;
}

.search-bar__welcome {
    width: 100%;
    max-width: 500px;
    margin-bottom: 1.7em;
}

.community-img {
    width: 95%;
    max-width: 550px;
    margin-inline: auto;
}

@media (min-width: 900px) {
    .welcome {
        padding-top: 7em;
        flex-direction: row;
    }

    .welcome>div {
        width: 100%;
    }

    .welcome h2 {
        position: static;
        transform: translate(0, 0);
    }
}