.intro-container {
    width: 100%;
    background: #000000;
    color: #ffffff;
}

.intro {
    padding-top: 19em;
    padding-bottom: 12em;
    width: 90%;
    max-width: 1200px;
    margin-inline: auto;
    text-align: center;
}

.intro-child__1 {
    width: 100%;
}

.headings-container {
    position: relative;
    display: flex;
    align-items: flex-end;
    height: 4rem;
}

.intro .heading {
    position: absolute;
    display: block;
    font-size: 1.5rem;
    width: 100%;
    max-width: 27ch;
    left: 50%;
    transform: translate(-50%, 0); 
    animation-duration: 10s;
    animation-timing-function:cubic-bezier(0.19, 1, 0.22, 1);
    animation-iteration-count: infinite;
}



.intro-img {
    width: 300px;
    margin-inline: auto;
    opacity: .2;
    margin-top: -320px;
}

.intro a {
    background: var(--clr-main);
    font-size: 1.05rem;
    color: #000000;
    padding: .7em 1.5em;
    border: 2px solid var(--clr-main);
    border-radius: 2px;
    margin-top: 1.2em;
}

.intro a:hover {
    box-shadow: 0 0.5em 0.5em -0.4em var(--clr-main);
    transform: translateY(-0.25em);
}

.heading-1 {
    animation-name: anim-1;
}

.heading-2 {
    animation-name: anim-2;
}

.heading-3 {
    animation-name: anim-3;
}

    
@keyframes anim-1 {

    0%,
    8.3% {
        bottom: -100%;
        opacity: 0;
    }

    8.3%,
    25% {
        bottom: 0;
        opacity: 1;
    }

    33.33%,
    100% {
        bottom: 110%;
        opacity: 0;
    }
}

@keyframes anim-2 {

    0%,
    33.33% {
        bottom: -100%;
        opacity: 0;
    }

    41.63%,
    58.29% {
        bottom: 0;
        opacity: 1;
    }

    66.66%,
    100% {
        bottom: 110%;
        opacity: 0;
    }
}

@keyframes anim-3 {

    0%,
    66.66% {
        bottom: -100%;
        opacity: 0;
    }

    74.96%,
    91.62% {
        bottom: 0;
          
              opacity: 1;
    }

    100% {
        bottom: 110%;
        opacity: 0;
    }
}

@media (min-width: 767px) {
    .intro {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10em 0;
        text-align: left;
    }

    .intro .heading {
        left: 0;
        transform: translate(0, 0); 
    }

    .intro img {
        width: 330px;
        opacity: 1;
        margin-top: 0;
    }
}

@media (min-width: 1024px) {
    .intro .heading {
        font-size: 1.8rem;
    }

    .intro img {
        width: 400px;
    }
}
