.search-bar {
    max-width: 600px;
    display: flex;
    align-items: center;
    height: 3.3rem;
}

.search-btn {
    height: 90%;
    aspect-ratio: 1;
    background: #000000;
    border: none;
}

.search-btn img {
    margin-inline: auto;
    width: 45%;
}

.search-input {
    width: 100%;
    font-size: .9rem;
    border: none;
    border-bottom: 1px solid #000000;
    padding: .87em;
}

@media (min-width: 1024px) {
    .search-input{
        padding: .86em;
    }
}