.suggestions-container {
    display: flex;
    flex-wrap: wrap;
    max-width: 445px;
    align-items: flex-end;
    gap: .6em;
    color: #909090;
    font-size: .75rem;
}

.suggestions-container > div:not(.more) {
    border: 1px solid #DEDEDE;
    border-radius: 2px;
    padding: .3em .7em;
}
