.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    padding: 1.5em 2.5em;
    margin-bottom: 1.1em;
    max-width: 400px;
        /* box-shadow: 0 0 10px 0 rgba(0,0,0,0.75); */
    border: 1px solid #dedede;
    border-radius: 10px;
}

.card h4 {
    color: var(--clr-main);
    font-size: 1.2rem;
    font-weight: 500;
}

.card img {
    width: 160px;
}

.card p {
    max-width:22ch;
}

@media (min-width: 900px) {
    .card {
        padding: 2em;
    }
}