/* ------------------- */
/* Custom properties   */
/* ------------------- */

:root {
    /* colors */
    --clr-main: #F9C933;
    
    /* font-sizes */
    --fs-600: 1.4rem;
    --fs-500: 1.1rem;
    --fs-400: 0.9375rem;
    --fs-300: 8rem;
    --fs-200: 0.7rem;
    
    /* font-families */
    --ff-sans: "Poppins", serif;
}

/* ------------------------------------ */
/* ------------- Utilities ------------ */
/* ------------------------------------ */

.fixed-position {
    overflow: hidden;
 }

 .txt-yellow {
    color: var(--clr-main);
 }


 /* ------------------------------------ */
/* ------------- Reset ----------------- */
/* ------------------------------------ */

*,
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'poppins', sans-serif;
    font-weight: 400;
}

body {
    overflow-x: hidden;
}
    
img {
    display: block;
}

ul {
    list-style-type: none;
}

a {
    display: inline-block;
    text-decoration: none;
}

button {
    cursor: pointer;
}

@media (min-width: 767px) {
    html {
        font-size:16.5px;
    }
}

@media (min-width: 1024px) {
    html {
        font-size: 18px;
    }
}

@media (min-width: 1600px) {
    html {
        font-size: 19px;
    }
}

@media (min-width: 1900px) {
    html {
        font-size: 21px;
    }
}